@font-face
	font-family: "svgfont"
	src: url('fonts/svgfont.eot')
	src: url('fonts/svgfont.eot?#iefix') format('eot'), url('fonts/svgfont.woff') format('woff'), url('fonts/svgfont.woff2') format('woff2'), url('fonts/svgfont.ttf') format('truetype')
	font-weight: normal
	font-style: normal

$icon-i-close: "\EA01"
$icon-i-apple: "\EA02"
$icon-i-arr: "\EA03"
$icon-i-arrow: "\EA04"
$icon-i-g: "\EA06"


%icon
	font-family: "svgfont"
	font-style: normal
	font-weight: normal
	text-rendering: auto
	speak: none
	line-height: 1
	-webkit-font-smoothing: antialiased
	-moz-osx-font-smoothing: grayscale

.icon, [class^="icon-"], [class*=" icon-"]
	@extend %icon
	display: inline-block

.icon-i-close:before
	content: $icon-i-close
.icon-i-apple:before
	content: $icon-i-apple
.icon-i-arr:before
	content: $icon-i-arr
.icon-i-arrow:before
	content: $icon-i-arrow
.icon-i-g:before
	content: $icon-i-g